<template>
  <div class="container content">
    <div class="row">
      <div class="col-12">
        <h1 class="text-primary">Pembelian Diamond Mobile Legend</h1>
      </div>
    </div>
    <div class="row mt-3">
      <div class="col-12">
        <div class="card bg-putih shadow rounded-10">
          <div class="card-body">
            <form class="text-left">
              <div class="form-group">
                <label for="no_tujuan" class="text-primary">ID Game</label>
                <input
                  @input="checkProvider"
                  v-model="this.noTujuan"
                  id="no_tujuan"
                  required=""
                  autocomplete="off"
                  type="text"
                  class="
                    form-control form-control-lg
                    border border-primary
                    rounded-10
                  "
                  name="no_hp"
                  pattern="\d*"
                  placeholder="Masukkan ID Game.."
                />
              </div>
              <!-- <div>
                <router-link
                  to="/customer/transaksi/piu2"
                  type="submit"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Lanjut
                </router-link>
              </div> -->
              <div class="form-group">
                <label for="produk"
                  >Produk
                  <span
                    v-show="this.user.verified == '0'"
                    class="badge badge-danger"
                    >Saldo : Rp. {{ numberFormat(this.user.saldo) }}</span
                  ></label
                >
                <select
                  v-model="idProduct"
                  id="produk"
                  name="produk"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option value="">Pilih</option>
                  <template v-for="product in orderedProduct" :key="product">
                    <option
                      :value="product.product_id"
                      v-if="product.product_id != 'TLOPC'"
                    >
                      {{ product.product_detail }} Harga: Rp.
                      {{ numberFormat(product.product_price + 2000) }}
                    </option>
                  </template>
                </select>
              </div>
              <div v-if="this.user.verified == '1'" class="form-group">
                <label for="metode_pembayaran">Metode Pembayaran</label>
                <select
                  v-model="metodePembayaran"
                  id="metode_pembayaran"
                  name="metode_pembayaran"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option value="">Pilih</option>
                  <option :value="'Hutang'">Bayar Nanti</option>
                  <option :value="'Dompet'">
                    Dompet | Saldo Anda: Rp. {{ numberFormat(this.user.saldo) }}
                  </option>
                </select>
              </div>
              <div>
                <button
                  @click="kirim()"
                  type="button"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Kirim
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOperator } from "no-telp";
import Swal from "sweetalert2";
import axios from "axios";
import _ from "lodash";

export default {
  name: "CusTransaksiVGMLView",
  props: ["ipAdress"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      noTujuan: null,
      dataCheckNoTujuan: {},
      idOperator: null,
      idProduct: null,
      metodePembayaran: null,
      dataLoadProduct: null,
    };
  },
  methods: {
    kirim() {
      if (this.user.verified == "0") {
        this.metodePembayaran = "Dompet";
      }
      if (!this.noTujuan) {
        Swal.fire({
          icon: "error",
          title: "No Tujuan",
          text: "silahkan masukkan no hp yang benar!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.idProduct) {
        Swal.fire({
          icon: "error",
          title: "Produk Kosong",
          text: "silahkan pilih produk!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.metodePembayaran) {
        Swal.fire({
          icon: "error",
          title: "Metode Pembayaran Kosong",
          text: "silahkan pilih metode pembayaran!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        Swal.fire({
          title: "Yakin untuk mengirim?",
          text: "Pastikan data form sudah benar!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yakin",
        }).then((result) => {
          if (result.isConfirmed) {
            const formData = new FormData();
            formData.append("metode_pembayaran", this.metodePembayaran);
            formData.append("no_hp", this.noTujuan);
            formData.append("produk", this.idProduct);
            formData.append("operatorProduk", this.idOperator);
            formData.append("cus_id", this.user.id);

            axios({
              method: "post",
              url:
                this.ipAdress +
                "api/transaksi/vgml?username=" +
                this.user.username,
              data: formData,
              headers: { "Content-Type": "multipart/form-data" },
            })
              .then(function (response) {
                //handle success
                // console.log(response);
                if (response.data.status == "failed") {
                  Swal.fire({
                    icon: "error",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else if (response.data == "") {
                  Swal.fire({
                    icon: "success",
                    title: "Diproses",
                    text: "Transaksi Diproses",
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else {
                  Swal.fire({
                    icon: "success",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                }
              })
              .catch(function (response) {
                //handle error
                console.log(response);
              });
          }
        });
      }
    },
    loadProduct() {
      axios
        .get(
          this.ipAdress +
            "api/get-prabayar-produk/" +
            this.idOperator +
            "?username=" +
            this.user.username
        )
        .then(({ data }) => {
          this.dataLoadProduct = data;
          // console.log(data);
        });
    },

    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },

    checkProvider: function () {
      this.idOperator = "VGML";
      this.loadProduct();
    },
  },
  computed: {
    orderedProduct: function () {
      return _.orderBy(this.dataLoadProduct, "product_price", "asc");
    },
  },
  mounted() {},
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}
#logo-operator {
  width: 30%;
}
</style>